@import url(https://fonts.googleapis.com/css?family=Nunito|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  color: #fff;
}

.title {
  font-size: 1em;
  color: #769EC1;
}

/* Assign grid instructions to our parent grid container */
.App {
  display: grid;
  grid-template-columns: [sidenav] 1fr [conteudo] 14fr;
  grid-template-rows: [header-page] 100px [body] 100vh;
  grid-gap: 15px;
  /*align-items: stretch; Alinha conforme o limite inferior de rolagem*/ 
  background-image: -webkit-gradient(linear, left bottom, left top, from(#19222f), to(#002544)); 
  background-image: linear-gradient(to top, #19222f, #002544);
  padding-right: 15px;
  padding-bottom: 15px;
  align-items: strech;
  /*overflow: hidden;*/
}

/* Give every child element its grid name */
.header-page {
  display: grid;
  grid-column: conteudo;
  grid-row: header-page;
  grid-template-columns: [left-header] 1.8fr [right-header] 1fr;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.06);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#19222f), to(#002544));
  background-image: linear-gradient(to bottom, #19222f, #002544);
  align-items: center;
  padding-left: 15px;
}

.header-page h1 {
  font-size: 18px;
  text-transform: capitalize;
}

.header-page h2 {
  font-size: .7em;
}

.right-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.right-header:hover img {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  -webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}


/* Container ----- */
.sidenav {
  display: grid;
  grid-column: sidenav;
  grid-row: header-page / span 2;
  grid-template-rows: 1fr 8fr;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.06);
  background-image: -webkit-gradient(linear, left bottom, left top, from(#19222f), to(#002544));
  background-image: linear-gradient(to top, #19222f, #002544);
  justify-items: center;
  justify-content: space-evenly;
}


.sidenav img:first-child {
  margin-top: 35px;
  flex-direction: column;
}

.sidenav ul {
  list-style-type: none;
}

@-webkit-keyframes swing
{
    15%
    {
        -webkit-transform: translateX(5px);
        transform: translateX(5px);
    }
    30%
    {
        -webkit-transform: translateX(-5px);
       transform: translateX(-5px);
    } 
    50%
    {
        -webkit-transform: translateX(3px);
        transform: translateX(3px);
    }
    65%
    {
        -webkit-transform: translateX(-3px);
        transform: translateX(-3px);
    }
    80%
    {
        -webkit-transform: translateX(2px);
        transform: translateX(2px);
    }
    100%
    {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}
@keyframes swing
{
    15%
    {
        -webkit-transform: translateX(5px);
        transform: translateX(5px);
    }
    30%
    {
        -webkit-transform: translateX(-5px);
        transform: translateX(-5px);
    }
    50%
    {
        -webkit-transform: translateX(3px);
        transform: translateX(3px);
    }
    65%
    {
        -webkit-transform: translateX(-3px);
        transform: translateX(-3px);
    }
    80%
    {
        -webkit-transform: translateX(2px);
        transform: translateX(2px);
    }
    100%
    {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

.sidenav li:hover {
  -webkit-animation: swing 1s ease;
  animation: swing 1s ease;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}

#Ampulheta {
  -webkit-transition: all 0.8s ease-in-out 0s;
  transition: all 0.8s ease-in-out 0s;
}

#Ampulheta:hover {
  -webkit-transform: rotateZ(-180deg);
  transform: rotateZ(-180deg);
}


/* Container ----- */
.body {
  display: grid;
  grid-column: conteudo;
  grid-gap: 15px;
  grid-row: body;
  grid-template-columns: [overview] 1.2fr [meditation-column] 1fr [stress] 1fr;
  grid-template-rows: [meditation-row] 1fr [intolerance] 1fr;
  height: 100vh;
}

.body ul {
  display: flex;
  list-style-type: none;
  justify-content: space-evenly;
}

ul li a {
  position: relative;
  display: block;
  text-decoration: none;
  color: #ecf0f1;
  padding: 4px 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
ul li a::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 3px;
  top: 100%;
  left: 0;
  background: #ff7000;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: right;
          transform-origin: right;
}
ul li a:hover {
  color: #95a5a6;
}
ul li a:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: left;
          transform-origin: left;
}



